,[_vm._v("Calzado de campamento")]),_c('p',{staticClass:"mb-0"},[_vm._v("Sandalias o zapatos ligeros y cómodos para usar en el campamento, permitiendo que los pies descansen después de la caminata. Deben ser fáciles de empacar y ofrecer buena ventilación. Las sandalias con suela antideslizante son una excelente opción, ya que permiten movilidad en terrenos irregulares del campamento.")])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"h4"},[_vm._v("Aislante térmico")]),_c('p',{staticClass:"mb-0"},[_vm._v("Esterilla o colchoneta que proporcione aislamiento del suelo frío o húmedo. Un aislante térmico de espuma o inflable es ideal, ya que mejora la comodidad y ayuda a mantener el calor al dormir. Es recomendable elegir un aislante compacto y liviano para facilitar su transporte.")])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"h4"},[_vm._v("Protección contra la lluvia")]),_c('p',{staticClass:"mb-0"},[_vm._v("Plástico o poncho impermeable de buen tamaño para proteger tanto a la persona como a la mochila en caso de lluvia. Si se espera lluvia intensa, es ideal contar con un poncho resistente y de alta cobertura, o con fundas impermeables para la mochila.")])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"h4"},[_vm._v("Sombrero o gorra")]),_c('p',{staticClass:"mb-0"},[_vm._v("Sombrero de ala ancha o cachucha para proteger la cara y el cuello del sol. Es ideal que el material sea ligero, transpirable y con factor de protección solar (UPF). Los sombreros de ala ancha brindan mayor protección en climas soleados y calurosos.")])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"h4"},[_vm._v("Protección solar e insectos")]),_c('p',{staticClass:"mb-0"},[_vm._v("Bloqueador solar de amplio espectro (SPF 30 o superior) para evitar quemaduras solares, y repelente de insectos con ingredientes como DEET o picaridina, eficaces contra mosquitos y otros insectos. Se recomienda aplicarlos regularmente, especialmente en zonas de alta exposición al sol o con 